<template>
  <div class="markdown">
    <div class="title">
      {{title}}
    </div>
    <!--<img :src="imgUri" alt="">-->
    <div class="md-display page" v-html="mhtml"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  import marked from 'marked'
  import {highlightAuto} from 'highlight.js'
  import 'highlight.js/styles/arta.css'

  marked.setOptions({
    highlight: function (code) {
      return highlightAuto(code).value
    }
  })
  export default {
    components: {},
//    computed: {
//        imgUri () {
//            return `/static/markdown/${this.path.slice(0, -3)}.png`
//        }
//    },
    data () {
      return {
        path: '',
        md: '',
        mhtml: '',
        title: ''
      }
    },
    async created () {
      this.path = this.$route.params.path
      this.title = this.path.split('/').pop().slice(0, -3)
      this.md = (await axios(`/static/markdown/${this.path}`)).data
      this.md = this.md.replace(/(\[.*?\]\()((?!http).+\))/g, `$1/static/markdown/${this.path}/../$2`)
      this.mhtml = marked(this.md)
    }
  }
</script>

<style lang="scss">
  @import '../style/markdown';
</style>



